import React, { Component } from 'react';
import tp from 'tp-eosjs';
import { LocaleProvider, Layout, Spin, Icon, Dropdown, Menu, Button, Table, InputNumber, Drawer, Divider, message } from 'antd';
import zh_CN from 'antd/lib/locale-provider/zh_CN';
import logo from './img/logo.png';
import left_img from './img/left.svg';
import latest_info_img from './img/latest_info.png';
import latest_info_bg from './img/latest_info_bg.png';
import my_guess_img from './img/my_guess.png';
import players_img from './img/players.png';
import players_bg from './img/players_bg.png';
import winners_img from './img/winners.png';
import winners_bg from './img/winners_bg.png';
import funny from './img/funny.svg';
import './App.css';

const { Header, Content } = Layout;

const contract_account   = 'unionramgame';
const seconds_per_round  = 6 * 3600;  // 6 hours
const seconds_per_draw   = (15 * 60 + seconds_per_round);  // 15 minutes later afetr round end

Date.prototype.Format = function (fmt) { //author: meizz
  var o = {
      "M+": this.getMonth() + 1, //月份
      "d+": this.getDate(), //日
      "h+": this.getHours(), //小时
      "m+": this.getMinutes(), //分
      "s+": this.getSeconds(), //秒
      "q+": Math.floor((this.getMonth() + 3) / 3), //季度
      "S": this.getMilliseconds() //毫秒
  };
  if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
  for (var k in o)
      if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
  return fmt;
}

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      loading: false,
      walletList: [],
      walletSelect: '请选择账号',
      addressSelect: '',
      default_input_price: 0,
      ramprice: '0.00000000 EOS/KB',
      gamepool: '0.0000 EOS',
      game_starttime: 0,
      game_lastprice: '0.00000000 EOS/KB',
      gametime: '00:00:00',
      gamestatus: '即将开始：',
      gameaction: '即将开始',
      gamestatus_flag: 0,  // 0: 即将开始, 1: 进行中, 2: 即将开奖, 3: 等待开奖
      gameaction_btn: true,
      gameaction_input: true,
      players: [],
      winners: [],
      pw_fresh: false,
      guess_price: '12345678',
      guess_quantity: '0.2500 EOS',
      visibleGameIntro: false,
    }

    this.gameShare = this.gameShare.bind(this);
    this.checkTime = this.checkTime.bind(this);
    this.displayTimeDelta = this.displayTimeDelta.bind(this);

    this.tpGetEOSWallets = this.tpGetEOSWallets.bind(this);
    this.walletChange = this.walletChange.bind(this);
    this.fetchRamprice = this.fetchRamprice.bind(this);
    this.fetchRoundInfo = this.fetchRoundInfo.bind(this);

    this.quantityChange = this.quantityChange.bind(this);
    this.guessPriceChange = this.guessPriceChange.bind(this);

    this.fetchGamePlayers = this.fetchGamePlayers.bind(this);
    this.fetchGameWinners = this.fetchGameWinners.bind(this);

    this.btnGameAction = this.btnGameAction.bind(this);
    this.showGameIntro = this.showGameIntro.bind(this);
    this.closeGameIntro = this.closeGameIntro.bind(this);
  }

  checkTime = (i) => {
    if( i < 10 ) {i="0"+i; } return i;
  }

  displayTimeDelta = (seconds) => {
    var hour = parseInt(seconds/60/60, 10);
    var minute = parseInt(seconds/60%60, 10);
    var second = parseInt(seconds%60, 10);
    hour = this.checkTime(hour);
    minute = this.checkTime(minute);
    second = this.checkTime(second);
    return hour + ":" + minute + ":" + second;
  }

  tpGetEOSWallets = () => {
    tp.getWalletList('eos').then(obj => {
      const eosWallets = [];
      for (let wallet of obj.wallets.eos) {
        eosWallets.push(<Menu.Item key={wallet.address}>{wallet.name}</Menu.Item>);
      }
      this.setState({ walletList: eosWallets });
    }).catch(e => {
      console.error(e);
      message.error("获取钱包EOS账号失败");
    })
  }

  walletChange = (value) => {
    const account_name = value.item.props.children;
    const account_address = value.item.props.eventKey;
    if ( account_name.length === 12 && account_address.length === 53 ) {
      this.setState({ walletSelect: account_name });
      this.setState({ addressSelect: account_address });
    } else {
      message.info("请选择有效的EOS账号");
    }
  }

  quantityChange = (value) => {
    this.setState({ guess_quantity: value.toFixed(4) + ' EOS' });
  }

  guessPriceChange = (value) => {
    this.setState({ guess_price: parseInt(value * 100000000, 10).toFixed(0) });
  }

  btnGameAction = () => {
    if ( this.state.walletSelect.length === 12 && this.state.addressSelect.length === 53 ) {
      // gamestatus_flag  0: 即将开始, 1: 进行中, 2: 即将开奖, 3: 等待开奖
      if ( this.state.gamestatus_flag === 1 && this.state.walletSelect.length === 12 && this.state.addressSelect.length === 53) {
        tp.pushEosAction({
          actions: [{
            account: 'eosio.token',
            name: 'transfer',
            authorization: [{
              actor: this.state.walletSelect,
              permission: 'active'
            }],
            data: {
              from: this.state.walletSelect,
              to: contract_account,
              quantity: this.state.guess_quantity,
              memo: this.state.guess_price
            }
          }],
          address: this.state.addressSelect,
          account: this.state.walletSelect
        }).then(res => console.log(res));
      } else if ( this.state.gamestatus_flag === 3 ) {
        tp.pushEosAction({
          actions: [{
            account: contract_account,
            name: 'gamedraw',
            authorization: [{
              actor: this.state.walletSelect,
              permission: 'active'
            }],
            data: {
              owner: this.state.walletSelect
            }
          }],
          address: this.state.addressSelect,
          account: this.state.walletSelect
        }).then(res => {
          console.log(res);
        });
      }
    } else {
      message.info("请先选择账号");
    }
  }

  gameShare = () => {
    tp.shareNewsToSNS({
      title: 'EOSRAMGame',
      desc: 'EOS RAM Game',
      url: 'https://ramgame.io/',
      previewImage: 'https://ramgame.io/favicon.ico'
    }).then(res => {
      console.log(res);
    }).catch(e => {
      console.error(e);
    })
  }

  showGameIntro = () => {
    this.setState({ visibleGameIntro: true });
  }

  closeGameIntro = () => {
    this.setState({ visibleGameIntro: false });
  }

  fetchRamprice = () => {
    tp.getTableRows({
      json: true,
      code: 'eosio',
      scope: 'eosio',
      table: 'rammarket',
      lower_bound: '',
      limit: 1
    }).then(obj => {
      if ( obj.data.rows ) {
        this.setState({ ramprice: (1 * obj.data.rows[0].quote.balance.split(" ")[0] / (1 + obj.data.rows[0].base.balance.split(" ")[0] / 1024)).toFixed(8) + ' EOS/KB' });
        if (this.state.default_input_price === 0) {
          this.setState({ default_input_price: (1 * obj.data.rows[0].quote.balance.split(" ")[0] / (1 + obj.data.rows[0].base.balance.split(" ")[0] / 1024)).toFixed(8) });
        }
      }
    }).catch(e => {
      console.error(e);
      // message.error("fetch rammarket fail");
    })
    setTimeout(this.fetchRamprice, 3000);
  }

  fetchRoundInfo = () => {
    tp.getTableRows({
      json: true,
      code: contract_account,
      scope: contract_account,
      table: 'round',
      lower_bound: '',
      limit: 1
    }).then(obj => {
      if ( obj.data.rows ) {
        let gamestart = 1 * obj.data.rows[0].start_time;
        let seconds_now = parseInt(new Date().getTime() / 1000, 10);
        if ( seconds_now < gamestart ) {
          this.setState({ gametime: this.displayTimeDelta(gamestart-seconds_now) });
          this.setState({ gamestatus: '即将开始：' });
          this.setState({ gameaction: '即将开始' });
          this.setState({ gameaction_btn: true });
          this.setState({ gameaction_input: true });
          this.setState({ gamestatus_flag: 0 });
          if ( this.state.pw_fresh ) {
            let _players = [];
            this.setState({ players: _players });
            this.fetchGameWinners();
          }
        } else if ( seconds_now >= gamestart && seconds_now < gamestart + seconds_per_round ) {
          this.setState({ gametime: this.displayTimeDelta(gamestart+seconds_per_round-seconds_now) });
          this.setState({ gamestatus: '进行中：' });
          this.setState({ gameaction: '提交' });
          this.setState({ gameaction_btn: false });
          this.setState({ gameaction_input: false });
          this.setState({ gamestatus_flag: 1 });
        } else if ( seconds_now >= gamestart + seconds_per_round && seconds_now < gamestart + seconds_per_draw ) {
          this.setState({ gametime: this.displayTimeDelta(gamestart+seconds_per_draw-seconds_now) });
          this.setState({ gamestatus: '即将开奖：' });
          this.setState({ gameaction: '即将开奖' });
          this.setState({ gameaction_btn: true });
          this.setState({ gameaction_input: true });
          this.setState({ gamestatus_flag: 2 });
        } else if ( seconds_now >= gamestart + seconds_per_draw ) {
          this.setState({ gametime:  new Date(seconds_now*1000).Format('MM-dd hh:mm:ss') });
          this.setState({ gamestatus: '等待开奖：' });
          this.setState({ gameaction: '请开奖' });
          this.setState({ gameaction_btn: false });
          this.setState({ gameaction_input: true });
          this.setState({ gamestatus_flag: 3 });
          this.setState({ pw_fresh: true });
        }

        this.setState({ gamepool: obj.data.rows[0].gamepool });
        this.setState({ game_starttime: gamestart });
        this.setState({ game_lastprice: obj.data.rows[0].last_ramprice });
      }
    }).catch(e => {
      console.error(e);
      // message.error("fetch round info fail");
    })

    setTimeout(this.fetchRoundInfo, 1000);
  }

  fetchGamePlayers = () => {
    let _bound = ''
    let _len = this.state.players.length;
    if (_len > 0) {
      _bound = (this.state.players[_len-1].id + 1).toFixed(0);
    }
    tp.getTableRows({
      json: true,
      code: contract_account,
      scope: contract_account,
      table: 'players',
      lower_bound: _bound,
      limit: 300
    }).then(obj =>{
      if ( obj.data.rows ) {
        let _players = this.state.players;
        for (let _player of obj.data.rows) {
          _players.push(_player);
        }
        this.setState({ players: _players });
      }
    }).catch(e => {
      console.error(e);
    })

    setTimeout(this.fetchGamePlayers, 2000);
  }

  fetchGameWinners = () => {
    tp.getTableRows({
      json: true,
      code: contract_account,
      scope: contract_account,
      table: 'winners',
      lower_bound: '',
      limit: 1300
    }).then(obj =>{
      if ( obj.data.rows ) {
        let _winners = [];
        for (let _winner of obj.data.rows) {
          _winners.push(_winner);
        }
        this.setState({ winners: _winners });
      }
    }).catch(e => {
      console.error(e);
    })
  }

  componentWillMount() {
    this.tpGetEOSWallets();
    this.fetchRamprice();
    this.fetchRoundInfo();
  }
  componentDidMount() {
    this.fetchGamePlayers();
    this.fetchGameWinners();
  }

  render() {

    const menu = (
      <Menu style={{background:"#d9d9d9"}} onClick={this.walletChange}>
        {this.state.walletList}
      </Menu>
    );

    const columns_latest_info = [{
      title: 'State', dataIndex: 'state', align: 'right', width: '40%', className: "latest-info-columns",
      onCell: record => ({
        record,
        style: {padding: "10px 2px"}
      }),
    }, {
      title: 'Value', dataIndex: 'value', align: 'left', width: '60%', className: "latest-info-columns",
      onCell: record => ({
        record,
        style: {padding: "10px 2px"}
      }),
    }];

    const data_latest_info = [{
      key: '1', state: 'RAM价格：', value: this.state.ramprice,
    }, {
      key: '2', state: '奖池：', value: this.state.gamepool,
    },{
      key: '3', state: this.state.gamestatus, value: this.state.gametime,
    }];

    const columns_my_guess = [{
      title: 'State', dataIndex: 'state', align: 'right', width: '40%', className: "my-guess-columns",
      onCell: record => ({
        record,
        style: {padding: "10px 2px"}
      }),
    }, {
      title: 'Value', dataIndex: 'value', align: 'left', width: '60%', className: "my-guess-columns",
      onCell: record => ({
        record,
        style: {padding: "10px 2px"}
      }),
      render: (text, record, index) => (
        <div>
          {
            index === 0 ? <InputNumber min={0.25} max={100} step={0.1} defaultValue={0.2500} precision={4} onChange={this.quantityChange} disabled={this.state.gameaction_input}/> :
            <InputNumber min={0.00000001} max={9.99999999} step={0.00000001} defaultValue={0.12345678} precision={8} onChange={this.guessPriceChange} disabled={this.state.gameaction_input}/>
          }
        </div>
      ),
    }];

    const data_my_guess = [{
      key: '1', state: '金额：',
    }, {
      key: '2', state: '竞猜价格：',
    }];

    const columns_players = [{
      title: '账号', dataIndex: 'account', align: 'center', width: "30%",
    }, {
      title: '金额(EOS)', dataIndex: 'quantity', align: 'center', width: "20%",
    }, {
      title: '价格(EOS/KB)', dataIndex: 'ramprice', align: 'center', width: "26%",
    }, {
      title: '交易时间', dataIndex: 'txtime', align: 'center', width: "24%",
    }];

    const len_players = this.state.players.length;
    const data_players = [];
    for ( let i = len_players -1; i >= 0; i-- ) {
      data_players.push({
        key: i,
        account: this.state.players[i].owner,
        quantity: this.state.players[i].tx_quantity.split(' ')[0],
        ramprice: this.state.players[i].tx_ramprice.split(' ')[0],
        txtime: new Date(1000 * this.state.players[i].tx_time).Format('MM/dd hh:mm:ss'),
      });
    }

    const columns_winners = [{
      title: '账号', dataIndex: 'account', align: 'center', width: "30%",
    }, {
      title: '中奖(EOS)', dataIndex: 'prize', align: 'center', width: "24%",
    }, {
      title: '买入(EOS)', dataIndex: 'quantity', align: 'center', width: "22%",
    }, {
      title: '交易时间', dataIndex: 'txtime', align: 'center', width: "24%",
    }];

    const len_winners = this.state.winners.length;
    const data_winners = [];
    for ( let i = len_winners - 1; i >= 0; i-- ) {
      data_winners.push({
        key: i,
        account: this.state.winners[i].owner,
        prize: this.state.winners[i].win_quantity.split(' ')[0],
        quantity: this.state.winners[i].tx_quantity.split(' ')[0],
        txtime: new Date(1000 * this.state.winners[i].tx_time).Format('MM/dd hh:mm:ss'),
      });
    }

    const container_else = (
      <div className="App">
        <header className="App-header">
          <img src={funny} className="App-logo" alt="to have fun" />
          <h1 className="App-title">Welcome to EOS RAM GAME</h1>
        </header>
        <p className="App-intro">
          请在TP钱包开发者模式打开该链接 LOLZzz.
        </p>
        {/* <a onClick={this.showGameIntro}><img src={left_img} alt="left" height="20" width="20"/></a> */}
        <Drawer
          title="游戏简介"
          placement="left"
          closable={true}
          width="90%"
          onClose={this.closeGameIntro}
          visible={this.state.visibleGameIntro}
          style={{ background: '#11213C', color: '#fff', fontSize: '18px', height: '100vh' }}
        >
          <p>Some contents...</p>
          <p>Some contents...</p>
          <p>Some contents...</p>
        </Drawer>
      </div>
    );

    const container_mobile_tp = (
      <Layout>
        <Header style={{ position: 'fixed', zIndex: 1, width: '100%' }}>
          <img src={logo} alt="logo" height='34' width='122'/>
        </Header>
        <Content style={{ marginTop: '54px' }}>
          <div style={{ padding: '10px 10px 10px 10px' }}>
            <a onClick={this.showGameIntro}><img src={left_img} alt="left" height="20" width="20"/></a>
            <Dropdown overlay={menu} trigger={['click']}>
              <Button style={{ float: "right", borderRadius: "34px", color: "#1E88FF", background: "#11213C", borderColor: "#1E88FF" }}>
                <Icon type="user"/>{this.state.walletSelect}<Icon type="down" />
              </Button>
            </Dropdown>
          </div>
          <div style={{ textAlign: "center", fontSize: "16px", color: "#FFFFFF", paddingTop: '30px', paddingBottom: "10px" }}>
            <img src={latest_info_img} alt="latest info" height="20" width="20" />
            <span>&nbsp;&nbsp;实时信息参考</span>
          </div>
          <div style={{ margin: "auto", width: "95%",background: "url("+latest_info_bg+")", backgroundRepeat: "no-repeat", backgroundSize: "100% 100%" }}>
            <Table
              showHeader={false}
              pagination={false}
              columns={columns_latest_info}
              dataSource={data_latest_info}
            />
          </div>
          <div style={{ textAlign: "center", fontSize: "16px", color: "#FFFFFF", paddingTop: '30px' }}>
            <img src={my_guess_img} alt="my guess" height="20" width="20" />
            <span>&nbsp;&nbsp;我的竞猜</span>
          </div>
          <div style={{ margin: "auto", width: "85%" }}>
            <Table
              showHeader={false}
              pagination={false}
              columns={columns_my_guess}
              dataSource={data_my_guess}
            />
          </div>
          <div style={{ margin: "auto", width: "80%", paddingTop: "10px" }}>
            <Button style={{ borderRadius: "34px", color: "#1E88FF", background: "#11213C", borderColor: "#1E88FF", width: "100%", height: "38px" }} onClick={this.btnGameAction} disabled={this.state.gameaction_btn}>
              {this.state.gameaction}
            </Button>
          </div>
          <div style={{ textAlign: "center", fontSize: "16px", color: "#FFFFFF", paddingTop: '40px', paddingBottom: "10px" }}>
            <img src={players_img} alt="my guess" height="20" width="20" />
            <span>&nbsp;&nbsp;实时玩家</span>
          </div>
          <div style={{ margin: "auto", width: "95%",background: "url("+players_bg+")", backgroundRepeat: "no-repeat", backgroundSize: "100% 100%", minHeight: "320px" }}>
            <Table
              pagination={false}
              scroll={{y:320}}
              columns={columns_players}
              dataSource={data_players}
            />
          </div>
          <div style={{ textAlign: "center", fontSize: "16px", color: "#FFB51C", paddingTop: '40px', paddingBottom: "10px", fontWeight: "bold" }}>
            <p>上期开奖: {new Date((this.state.game_starttime-5*60)*1000).Format('MM-dd hh:mm:ss')}</p>
            <p>上期价格: {this.state.game_lastprice}</p>
          </div>
          <div style={{ textAlign: "center", fontSize: "16px", color: "#FFFFFF", paddingTop: '10px', paddingBottom: "10px" }}>
            <img src={winners_img} alt="my guess" height="20" width="20" />
            <span>&nbsp;&nbsp;排行榜</span>
          </div>
          <div style={{ margin: "auto", width: "95%",background: "url("+winners_bg+")", backgroundRepeat: "no-repeat", backgroundSize: "100% 100%", minHeight: "320px" }}>
            <Table
              pagination={false}
              scroll={{y:320}}
              columns={columns_winners}
              dataSource={data_winners}
            />
          </div>
          <div style={{ textAlign: "center", paddingTop: '40px', paddingBottom: "20px"}}>
            <span style={{fontSize: "10px", color: "#d9d9d9"}}>©2018-2019 TeamRG@RAMGame TeamRG@RAMGame 2018</span>
          </div>
          <Drawer
            title="游戏简介"
            placement="left"
            closable={true}
            width="100%"
            onClose={this.closeGameIntro}
            visible={this.state.visibleGameIntro}
            style={{ background: '#11213C', color: '#d9d9d9', fontSize: '12px' }}
          >
            <Divider style={{ color: "#d9d9d9" }}>玩法</Divider>
            <p>1. 游戏开始时，玩家对开奖时RAM价格进行竞猜，提交竞猜价格和金额。开奖后中奖的玩家瓜分该轮奖池的奖金。</p>
            <Divider style={{ color: "#d9d9d9" }}>规则</Divider>
            <p>1. 每次竞猜的提交金额最少为0.25个EOS。</p>
            <p>2. 竞猜的RAM价格的精度为小数点后8位。</p>
            <p>3. 每轮游戏同一账号可以多次提交竞猜，如果都中奖，将按提交的时间、金额分别发放奖金。</p>
            <p></p>
            <Divider style={{ color: "#d9d9d9" }}>说明</Divider>
            <p>1. 每一轮游戏时间为6小时，游戏结束后该轮无法买入，等待开奖</p>
            <p>2. 为了防止机器人，每一轮游戏结束15分钟后(即6小时15分钟)方可开奖。开奖只能由参与该轮游戏的玩家或部署合约的账号执行，如果该轮没有玩家，那么任何人可执行开奖。</p>
            <p>3. 开奖需要进行中奖运算，因此开奖账号将要承担这部分cpu资源，虽然cpu不需要购买，但每轮的开奖账号(部署合约的账号除外)依然将获得0.5个EOS作为奖励(如果奖池中没有则不发放)。</p>
            <p>4. 每轮开奖完成，下一轮游戏将在5分钟之后可以提交买入。</p>
            <p>5. 如果开奖时的价格没有玩家猜中，那么最接近该价格的提交将中奖。如开奖时的价格为0.88888888, 如果游戏中最接近的竞猜为0.77777777和0.99999999，则这两个竞猜的提交都中奖。</p>
            <p>6. 每轮游戏合约方将抽取奖池10%。</p>
            <Divider style={{ color: "#d9d9d9" }}>奖金计算</Divider>
            <p>某个中奖提交获得的奖金 = (50% * A / B + 50% * C / D) * 奖池(减去合约方10%和开奖者0.5后)</p>
            <p>A: 该中奖提交的金额</p>
            <p>B: 所有中奖提交的金额之和</p>
            <p>C: 该中奖提交的交易时间距离游戏结束时间的距离(单位: 秒)。如, 游戏结束时间为18:00:00, 交易时间为17:50:00, 则C等于600</p>
            <p>D: 所有中奖提交的交易时间距离之和</p>
            <Divider></Divider>
            <p>合约开源，并且游戏中RAM价格、玩家提交记录等链上可查证，保证了游戏的公正透明</p>
            <p>合约源码: <a href="https://github.com/unionramgame/contract">https://github.com/unionramgame/contract</a></p>
            <p>游戏分享: <a onClick={this.gameShare}>https://ramgame.io/ (点击即可分享)</a></p>
            <p>客服微信: Eosxms</p>
          </Drawer>
        </Content>
      </Layout>
    );

    return (
      <div>
      <LocaleProvider locale={zh_CN}>
        {
          tp.isConnected() ? <Spin size="large" spinning={this.state.loading}>{container_mobile_tp}</Spin> :
          <Spin size="large" spinning={this.state.loading}>{container_else}</Spin>
        }
      </LocaleProvider>
      </div>
    );
  }
}

export default App;



